@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.movie {
  display: flex;
  height: 281px;
  overflow: hidden;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
}

.movie-info {
  font-weight: 400;
  font-style: normal;
  font-family: 'Inter', sans-serif;
}

.img-container {
  width: 200px;
  margin-right: 20px;
}

.img-container img {
  max-width: 200px;
  height: 100%;
}

.movie-info h2 {
  color: #000;
  font-size: 20px;
  line-height: 28px;
  margin: 12px 0 7px;
}

.movie-releaseDate {
  color: #827e7e;
  font-size: 12px;
  line-height: 22px;
  margin-bottom: 7px;
}

.movie-description {
  color: #000;
  font-size: 12px;
  line-height: 22px;
  margin: 7px 20px 49px 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.movieGenres {
  margin: 0;
  display: flex;
}

.movieGenres-genre {
  padding: 3px;
  font-size: 12px;
  margin-right: 8px;
  line-height: 14.52px;
  background-color: #fafafa;
  border: 1px solid #d9d9d9;
  color: rgba(0, 0, 0, 0.65);
}

.main {
  margin: 0 36px 37px;
  background-color: #fff;
}

.spinner {
  width: 40px;
  margin: 0 auto;
}

.stringRequest {
  width: 938px;
  font-size: 16px;
  padding: 9px 12px;
  line-height: 22px;
  margin: 34px 36px;
  font-family: Inter;
  border-radius: 5px;
  box-sizing: border-box;
  border: 1px solid #d9d9d9;
}

.stringRequest:focus,
.stringRequest:active {
  outline: none;
  transition: all 0.3s;
  border: 2px solid #40a9ff;
}

body {
	background: #E5E5E5 !important;
}

.movieApp {
  width: 1010px;
  margin: 0 auto;
	background-color: #fff;
}

.paginator {
	display: flex;
	justify-content: center;
	padding-bottom: 30px;
}

